import React from "react";
import { withRouteData, Link, Switch } from "react-static";
//

export default withRouteData(({ posts }) => (
  <Switch>
    <div>
      <h1>It's blog time.</h1>
      <br />
      All Posts:
      <ul>
        {posts.map(post => (
          <li key={post.id}>
            <Link to={`/blog/post/${post.id}/`}>{post.title}</Link>
          </li>
        ))}
      </ul>
    </div>
  </Switch>
));



